export default function drag() {
  const move = document.querySelector('#move')
  move.onmousedown = function (e) {
    const even = window.e||e
    let clientX = even.clientX
    console.log(clientX,'第一个x');
    let clientY = even.clientY
    let offleft = clientX - move.offsetLeft
    let offtop = clientY - move.offsetTop
    document.onmousemove  = function(e) {
      const even = window.e||e
      console.log(even.clientX,'第二个x');
      move.style.left = even.clientX - offleft +'px'
      move.style.top = even.clientY - offtop +'px'
    }
    document.onmouseup = function() {
        this.onmousemove = null
        this.onmousedown = null
        move.style.left = move.offsetLeft
        move.style.top = move.offsetTop

     }
  }
}